"use client"

import { useState } from 'react'
import Image from 'next/image'

const PRIMARY_IMG = 'https://i.imgur.com/sa4O982.png'
const FALLBACK_IMG = 'https://images.pexels.com/photos/607812/pexels-photo-607812.jpeg'

const IPhoneDisplay = () => {
  const [imgSrc, setImgSrc] = useState(PRIMARY_IMG)

  return (
    <div className="relative w-full max-w-[320px] mx-auto animate-float">
      {/* iPhone Frame */}
      <div className="relative w-[320px] h-[650px] mx-auto rounded-[50px] bg-black overflow-hidden shadow-[0_50px_100px_-20px_rgba(0,0,0,0.25),0_30px_60px_-30px_rgba(0,0,0,0.3)] border-[14px] border-black">
        {/* Notch */}
        <div className="absolute top-0 left-1/2 transform -translate-x-1/2 w-[40%] h-7 bg-black rounded-b-3xl z-50" />
        
        {/* Screen Content */}
        <div className="relative w-full h-full bg-white overflow-hidden rounded-[35px]">
          <Image
            src={imgSrc}
            alt="iPhone Screenshot"
            fill
            priority
            sizes="(max-width: 768px) 100vw, 320px"
            className="object-cover"
            onError={() => setImgSrc(FALLBACK_IMG)}
          />
        </div>

        {/* Volume Buttons (Left Side) */}
        <div className="absolute left-[-20px] top-[120px] w-[4px] h-[30px] bg-gray-600 rounded-l-lg" />
        <div className="absolute left-[-20px] top-[170px] w-[4px] h-[60px] bg-gray-600 rounded-l-lg" />

        {/* Power Button (Right Side) */}
        <div className="absolute right-[-20px] top-[120px] w-[4px] h-[60px] bg-gray-600 rounded-r-lg" />
      </div>
    </div>
  )
}

export default IPhoneDisplay
